<template>
    <font-awesome-icon :icon="icon" />
</template>

<script>
export default {
    name: 'StatusIcon',
    props: {
        status: {
            type: String
        }
    },
    computed: {
        icon() {
            if (this.status === 'success') return ['fas', 'circle-check']
            if (this.status === 'warning') return ['fas', 'circle-exclamation']
            if (this.status === 'danger') return ['fas', 'circle-xmark']
        }
    }
}
</script>
